import { css } from "@emotion/react"
import { Link } from "gatsby"
import AniLink from "gatsby-plugin-transition-link/AniLink"
import { colorScheme, dcr } from "../lib/color"

export default function Nav() {
  return(
    <nav css={styles.nav}>
      <NavItem icon="&#xe64d;" to="/" select />
      <NavItem icon="&#xe64a;" to="#" />
      <NavItem icon="&#xe63d;" to="#" />
    </nav>
  )
}

interface NavItemProps {
  icon: string;
  to: string;
  select?: boolean;
}

function NavItem({ icon, to, select }: NavItemProps) {
  return(
    <AniLink
      to={to}
      cover
      bg={colorScheme.back}
      css={[styles.navItem, css`
        opacity: ${select? 1 : 0.3};
      `]}
    >
      {icon}
    </AniLink>
  )
}

const styles = {
  nav: css`
    display: flex;
    flex-direction: column;
    align-items: center;
    position: fixed;
    top: 30px;
    width: 50px;
    background: ${dcr(0.05)};
    border-radius: 8px;
  `,
  navItem: css`
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 22px;
    width: 100%;
    height: 50px;
    color: inherit;
    user-select: none;
    text-decoration: none;
    font-family: 'iconfont';
    transition: opacity 200ms;
    :hover {
      opacity: 1;
    }
  `
}